import React, { useEffect } from 'react';
import * as d3 from 'd3';

const MyD3Component = () => {
  useEffect(() => {
    // D3 code here
    const svg = d3.select('#my-svg')
      .append('svg')
      .attr('width', 500)
      .attr('height', 500);
  
    svg.append('circle')
      .attr('cx', 250)
      .attr('cy', 250)
      .attr('r', 50)
      .attr('fill', 'red')
      .on('mouseover', function (d) {
        d3.select(this)
          .attr('fill', 'blue');
      })
      .on('mouseout', function (d) {
        d3.select(this)
          .attr('fill', 'red');
      });
  }, []);  // Empty dependency array means this useEffect runs once when component mounts
  
  return (
    <div id="my-svg"></div>
  );
};

export default MyD3Component;
